<template>
    <view>
        <view class="status-bar" :style="{
            height: statusBarHeight + 'px'
        }">
        </view>

        <view class="nav-bar" :style="{
            height: navHeight + 'px'
        }">
        	<text
			class="nav-bar-title">
				社区
			</text>
			<!-- #ifdef MP-WEIXIN -->
            <image 
			@click="$kt.util.navTo('/pages/public/publish-post')"
			:src="$kt.file.byPath('image/icon/send.svg')" :style="{
					right: (menuButton.width - (-15)) + 'px'
				}" mode="heightFix" class="nav-bar-icon">
			</image>
			<!-- #endif -->
			<!-- #ifdef APP-PLUS -->
            <image :src="$kt.file.byPath('image/icon/send.svg')" :style="{
					left: '670rpx'
				}" mode="heightFix" class="nav-bar-icon">
			</image>
			<!-- #endif -->
        </view>
		<view
		style="background-color: #fff;height: 10rpx;"
		></view>

        <view class="t">
                <text
                @click="tSelect='recommend'"
                :class="'tt '+(tSelect=='recommend'?'tt-active':'tt-no-active')">推荐</text>
                <text
                @click="tSelect='near'"
                :class="'tt '+(tSelect=='near'?'tt-active':'tt-no-active')">附近</text>
                <text
                @click="tSelect='follow'"
                :class="'tt '+(tSelect=='follow'?'tt-active':'tt-no-active')">关注</text>
        </view>

		<view
		style="background-color: #fff;height: 30rpx;"
		></view>

        <postRecommend
        v-show="tSelect=='recommend'"
        :statusBarHeight="statusBarHeight"
        :navHeight="navHeight"
        ></postRecommend>

        <postNear
        v-show="tSelect=='near'"
        :statusBarHeight="statusBarHeight"
        :navHeight="navHeight"
        ></postNear>

        <postFollow
        v-show="tSelect=='follow'"
        :statusBarHeight="statusBarHeight"
        :navHeight="navHeight"
        ></postFollow>
        
    
</view>
</template>

<script>
import postRecommend from './inCommunity/postRecommend.vue';
import postNear from './inCommunity/postNear.vue';
import postFollow from './inCommunity/postFollow.vue';

export default {
    components: {
        postRecommend,
        postNear,
        postFollow,
    },
    data() {
        return {
            statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
			navHeight: 48,
			// #ifdef MP-WEIXIN
            menuButton: wx.getMenuButtonBoundingClientRect(),
            // #endif
			screenTop: 0,
            tSelect: 'recommend',    
        };
    },
    mounted() {
        this.navHeight = 44;
        // #ifdef MP-WEIXIN
        this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
        // #endif
    },
    methods: {
    },
}
</script>

<style lang="scss" scoped>
.status-bar {
    background-color: #fff;
}

.nav-bar {
    position: relative;
    background-color: #fff;

    .nav-bar-icon {
        position: absolute;
        width: 47rpx;
        height: 47rpx;
        top: 50%;
        transform: translateY(-50%);
    }

    .nav-bar-icon:active {
        transform: scale(0.9) translateY(-50%);
    }

    .nav-bar-title {
        position: absolute;
        left: 20rpx;
        font-size: 42rpx;
        top: 50%;
        transform: translateY(-50%);
        // 文字间距
        // letter-spacing: 5rpx;
    }
}

.t {
    .tt {
        display: inline-block;
        font-size: 32rpx;
        margin-left: 30rpx;
        vertical-align: bottom;
    }

    .tt-no-active:active {
        opacity: 0.5;
    }

    .tt-active {
        font-weight: bold;
    }


}


</style>
